.loginContainer {
  padding-top: 150px;
  position: relative;
  height: 300px;
}
.login-label {
  display: block;
  letter-spacing: 4px;
  padding-top: 30px;
  text-align: center;
}

.label-text {
  color: #9b9b9b;
  cursor: text;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  transform: translateY(-34px);
  transition: all 0.3s;
}

.login-input {
  background-color: transparent;
  border: 0;
  border-bottom: 2px solid #4a4a4a;
  color: white;
  font-size: 36px;
  letter-spacing: -1px;
  outline: 0;
  padding: 5px 20px;
  text-align: center;
  transition: all 0.3s;
  width: 300px;
}

.login-input:focus {
  max-width: 100%;
  width: 500px;
}

.login-input:focus + .label-text {
  color: #f0f0f0;
  font-size: 13px;
  transform: translateY(-74px);
}

.login-input:valid + .label-text {
  font-size: 13px;
  transform: translateY(-74px);
}

.btnContainer {
  padding: 30px;
