.btn {
  opacity: 1;
  outline: 0;
  color: #fff;
  font-size: 24px;
  line-height: 40px;
  position: relative;
  text-align: center;
  letter-spacing: 1px;
  display: inline-block;
  text-decoration: none;
  font-family: "Open Sans";
  text-transform: uppercase;
  cursor: pointer;
}

.btn::after {
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  color: #323237;
  display: block;
  transition: 0.5s;
  position: absolute;
  background: #c3073f;
  content: attr(data-back);
  transform: translateY(-50%) rotateX(90deg);
}

.btn::before {
  top: 0;
  left: 0;
  opacity: 1;
  color: #adadaf;
  display: block;
  padding: 0 30px;
  line-height: 40px;
  transition: 0.5s;
  position: relative;
  background: #1a1a1d;
  content: attr(data-front);
  transform: translateY(0) rotateX(0);
}

.btn:hover::after {
  opacity: 1;
  transform: translateY(0) rotateX(0);
}

.btn:hover::before {
  opacity: 0;
  transform: translateY(50%) rotateX(90deg);
}

.errorMsg {
  color: rgb(194, 2, 2);
}

.btnContainer p {
  color: #9b9b9b;
}

.btnContainer span {
  color: white;
  cursor: pointer;
  border-bottom: 1px solid white;
}

