/* BRUGER */
.burger-container {
  margin-top: 10px;
  width: 40px;
  height: 40px;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
  cursor: pointer;
}

.burger {
  width: 40px;
  height: 4px;
  background-color: #c3073f;
  border-radius: 4px;
  margin-bottom: 8px;
  display: inline-block;
  transition: margin-left 0.6s ease-in;
  z-index: 200;
}

.burger.open {
  margin-left: 400px;
}

.burger:last-child {
  margin-bottom: 0px;
}

.burger-top.open {
  -webkit-animation: burger-top-open 0.6s ease-out forwards;
          animation: burger-top-open 0.6s ease-out forwards;
}

.burger-middle.open {
  -webkit-animation: burger-middle-open 0.6s linear forwards;
          animation: burger-middle-open 0.6s linear forwards;
}

.burger-bottom.open {
  -webkit-animation: burger-bottom-open 0.6s linear forwards;
          animation: burger-bottom-open 0.6s linear forwards;
}

@-webkit-keyframes burger-top-open {
  0% {
    width: 40px;
  }
  40% {
    background-color: #ff1456;
    width: 6px;
    transform: translate(40px, 0px);
  }
  75%,
  80% {
    width: 6px;
    transform: translate(40px, -50px);
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 1);
            animation-timing-function: cubic-bezier(0, 1, 1, 1);
  }
  100% {
    background-color: #ff1456;
    width: 6px;
    transform: translate(26px, 31px); /* DOT */
  }
}

@keyframes burger-top-open {
  0% {
    width: 40px;
  }
  40% {
    background-color: #ff1456;
    width: 6px;
    transform: translate(40px, 0px);
  }
  75%,
  80% {
    width: 6px;
    transform: translate(40px, -50px);
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 1);
            animation-timing-function: cubic-bezier(0, 1, 1, 1);
  }
  100% {
    background-color: #ff1456;
    width: 6px;
    transform: translate(26px, 31px); /* DOT */
  }
}

@-webkit-keyframes burger-middle-open {
  80% {
    background-color: #c3073f;
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    background-color: #ff1456;
    transform: translate(8px, -5px) rotate(45deg);
  }
}

@keyframes burger-middle-open {
  80% {
    background-color: #c3073f;
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    background-color: #ff1456;
    transform: translate(8px, -5px) rotate(45deg);
  }
}

@-webkit-keyframes burger-bottom-open {
  80% {
    background-color: #c3073f;
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    background-color: #ff1456;
    transform: translate(8px, -18px) rotate(-45deg);
  }
}

@keyframes burger-bottom-open {
  80% {
    background-color: #c3073f;
    transform: translate(0px, 0px) rotate(0deg);
  }
  100% {
    background-color: #ff1456;
    transform: translate(8px, -18px) rotate(-45deg);
  }
}

.burger-top.close {
  width: 8px;
  transform: translate(27px, 26px);
  -webkit-animation: burger-top-close 0.6s ease-out forwards;
          animation: burger-top-close 0.6s ease-out forwards;
}

.burger-middle.close {
  transform: translate(0px, 0px) rotate(40deg);
  -webkit-animation: burger-middle-close 0.6s ease-out forwards;
          animation: burger-middle-close 0.6s ease-out forwards;
}

.burger-bottom.close {
  transform: translate(0px, -23px) rotate(-40deg);
  -webkit-animation: burger-bottom-close 0.6s ease-out forwards;
          animation: burger-bottom-close 0.6s ease-out forwards;
}

@-webkit-keyframes burger-top-close {
  0%,
  70% {
    width: 0px;
  }
  100% {
    width: 40px;
    transform: translate(0, 0);
  }
}

@keyframes burger-top-close {
