.tab-btn {
  border: none;
  background-color: transparent;
  color: grey;
  width: 140px;
  height: 40px;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  position: relative;
  outline: none;
  border-radius: 3px;
  transition: color 0.5s, transform 0.2s, background-color 0.2s;
}

.tab-btn:active {
  transform: translateY(3px);
}

.tab-btn::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 100%;
  height: 100%;

  transition: opacity 0.3s, border 0.3s;
}
.tab-btn::after {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  width: 100%;
  height: 100%;
  color: #c3073f;
  background-color: transparent;
  border: 3px solid #c3073f;
  opacity: 0;
  z-index: 1;
  transform: scaleX(1.2) scaleY(1.4);
  transition: transform 0.3s, opacity 0.3s;
}

.tab-btn:hover {
  background-color: transparent;
  box-shadow: none;
  color: #c3073f;
}

.tab-btn.activated::before {
  border: 3px solid #c3073f;
}
.tab-btn:hover::after {
  opacity: 1;
  transform: scaleX(1) scaleY(1);
}

.activated {
  color: #c3073f;
}

